
.homeWithBanner {
  display: flex;
  flex-direction: column;
  margin: 50px 0 0 0;
  /*border: 1px solid red;*/
}

.homePage {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  margin: 50px 0 0 0;
}

.banner {
  justify-self: center;
  align-self: center;
  border: 1px solid #d7dadc;
  padding: 1vh 1vw;
  border-radius: 2px;
  /*margin-top: 5vh;*/
}
.deposits, .statistics {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  border-radius: 2px;
  border: 1px solid #D7DADC;
}
.statistics {
  width: 30vw;
  /*height: 40vh;*/
  height: 375px;
}
.deposits {
  /*height: 40vh;*/
  height: 375px;
}

.depWithHeader {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-bottom: 1px solid #D7DADC;
}

.recentTxns {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 150px;
  margin: 5px 0 0 0;
}

.tokDropDown {
  background-color: #1a1a1a;
  border-radius: 2px;
  width: 100%;
}

.tokDropDown {
  width: 29vw;
  text-align: center;
}

.tokName {
  width: 29vw;
  background-color: #1A1A1A;
  text-decoration: none;
}

.tokName:hover {
  color: #1F33E8;
}

@media only screen and (max-width: 514px) {
  .homeWithBanner {
    /*border: 1px solid green;*/
    width: 90vw;
    margin: 0 7vw;
    margin-top: 5vh;
  }
  .homePage {
    /*border: 1px solid blue;*/
    flex-direction: column;
  }
  .deposits, .statistics {
    margin: 20px 0 0 0;
    width: 90vw;
  }
  .banner {
    width: 90vw;
  }
}

.ddTitle {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  place-items: center;
}

.ddArrow {
  border: 2px solid #1F33E8;
  border-radius: 2px;
  border-right: 0;
  border-top: 0;
  margin-top: -.4em;
  pointer-events: none;
  transform: rotate(-45deg);
  transform-origin: center;
  height: .625em;
  width: .625em;
  right: 1.125em;
  transition: border-color .15s ease-in-out;
}

