.homeConnect:hover {
  background-color: #1F33E8;
}

.depWith:hover {
  color: #1F33E8;
}

.statsHeader {
  font-size: 18px;
  font-weight: bold;
  padding: 0 1vw;
}

.statsSubHeader {
  padding: 0 1vw;
  font-weight: bold;
}

.statsTokAmt {
  border: 1px solid #1F33E8;
  border-radius: 2px;
  font-size: 14px;
  margin: 0 0 0 4px;
  padding: 2px;
}

.statsIP {
  display: flex;
  justify-content: space-between;
  padding: 0 1vw;
}

.withdrawNote {
  resize: none;
  background-color: #1A1A1A;
  border-radius: 2px;
  border: 1px solid #1A1A1A;
  width: 92%;
  margin: 0 1vw;
  align-self: center;
  height: 75px;
}

.withdrawInput {
  background-color: #1A1A1A;
  border-radius: 2px;
  border: 1px solid #1A1A1A;
  width: 92%;
  margin: 0 1vw;
  align-self: center;
}

.withdrawRecipHeader {
  display: flex;
  justify-content: space-between;
  padding: 0 1vw;
  margin: 0.5vh 0;
}

.compliance {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 5vh 15vw;
}
.complianceHeader {
  font-size: 25px;
  margin: 5vh 0;
  font-weight: bold;
}
.inputEl {
  color: #D7DADC;
  background-color: #262626;
  border: 1px solid #262626;
  border-radius: 2px;
}
