.nav {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  border: 1px solid #D7DADC;
  border-radius: 2px;
  height: 50px;
  width: 75vw;
  margin: 0 auto;
}

@media only screen and (max-width: 514px) {
  .nav {
    width: 90vw;
  }
}

#basic-nav-dropdown,
.navl {
  text-decoration: none;
}

.navdd {
  /*background-color: #1a1a1a;*/
  background-color: darkorchid;
  border-radius: 2px;
  width: 100%;
}

.navd {
  background-color: #1A1A1A;
  width: 100%;
  /*border: 1px solid red;*/
  padding: 0 .5vw;
  text-align: center;
  text-decoration: none;
}

.ddArrow {
  border: 2px solid #1F33E8;
  border-radius: 2px;
  border-right: 0;
  border-top: 0;
  margin-top: -.4em;
  pointer-events: none;
  transform: rotate(-45deg);
  transform-origin: center;
  height: .625em;
  width: .625em;
  right: 1.125em;
  transition: border-color .15s ease-in-out;
}

#dd-btn {
  width: 29vw;
  background-color: transparent;
  border: 1px solid #1F33E8;
  border-radius: 2px;
  padding: 0 1vw;
  margin: 0 .5vw;
  font-size: 16px;
}
